/* eslint-disable no-fallthrough */
//
import axios from 'axios'
import { Toast } from 'vant'
axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
axios.defaults.baseURL = '/api'
axios.interceptors.request.use(
  config => {
    config.headers.client = 'pc'
    if (localStorage.getItem('token')) {
      config.headers.Authorization = 'Bearer ' + localStorage.getItem('token')
    }
    return config
  },
  err => {
    return Promise.reject(err)
  })

// http response 401token
axios.interceptors.response.use(
  response => {
    return response.data
  },
  error => {
    console.log('error.response', error.response)
    if (error.response) {
      switch (error.response.status) {
        case 401:
          // token
          localStorage.clear()
          Toast(error.response.data.message)
        case 400:
          localStorage.clear()
          Toast(error.response.data.message)
        //   sessionStorage.clear()
        //   Message.error(error.response.data.message)
        //   router.replace({
        //     path: '/login',
        //     query: {redirect: router.currentRoute.fullPath} // 
        //   })
        case 500:
          // token
          localStorage.clear()
          Toast(error.response.data.message)
          // 
      }
    }
    return Promise.reject(error.response.data)
  })
let func = {
  ajaxGet (api, query, cb) {
    axios.get(api, {
      params: query
    })
      .then(cb)
      .catch(err => {
        console.log(err)
      })
  },

  ajaxPost (api, post, cb) {
    axios.post(api, post)
      .then(cb)
      .catch(error => {
        console.log(error)
      })
  },
  ajaxPost2 (api, post, cb) {
    axios.post(api, post, {
      headers: {
        'client': 'app'
      }
    }
    )
      .then(cb)
      .catch(error => {
        console.log(error)
      })
  },
  ajaxPatch (api, patch, cb) {
    axios.patch(api, patch)
      .then(cb)
      .catch(error => {
        console.log(error)
      })
  },

  ajaxDelete (api, query, cb) {
    axios.delete(api, { params: query })
      .then(cb)
      .catch(error => {
        console.log(error)
      })
  },

  setDocumentTitle (title) {
    document.title = title
    if (/ip(hone|od|ad)/i.test(navigator.userAgent)) {
      var i = document.createElement('iframe')
      i.src = '/favicon.ico'
      i.style.display = 'none'
      i.onload = function () {
        setTimeout(function () {
          i.remove()
        }, 9)
      }
      document.body.appendChild(i)
    }
  }
}

export default func



